/* width */
::-webkit-scrollbar {
    width: 5px;
    height: 5px;
    position: absolute;
}

/* Track */
::-webkit-scrollbar-track {
    background: rgb(230, 230, 230); 
    border-radius: 100px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: rgb(169, 169, 169); 
    border-radius: 100px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: rgb(133, 133, 133); 
}
